import { Component, OnInit } from '@angular/core';
import { DatePipe } from '@angular/common';
import { FeedsMedicineConsumptionService } from './../../../../services/feeds-medicine-consumption/feeds-medicine-consumption.service'
import { AuthService } from './../../../../services/auth/auth.service';

@Component({
  selector: 'app-feeds-medicine-consumption-listing',
  templateUrl: './feeds-medicine-consumption-listing.component.html',
  styleUrls: ['./feeds-medicine-consumption-listing.component.scss']
})
export class FeedsMedicineConsumptionListingComponent implements OnInit {

  isLoaded: boolean = false;
  monthdatetoday: any = new Date();
  page: number = 1;
  limit: number = 10;
  totalItems: number = 0;
  prev_page: number = 0;
  next_page: number = 0;
  totalPages: number = 0;
  limit_disabled: number = 0;
  houses: any = [];

  canAdd: boolean = false;
  constructor(
    private auth: AuthService,
    private datePipe: DatePipe,
    private feedsandmedicineConsumption: FeedsMedicineConsumptionService
  ) {
    
  }

  ngOnInit() {
    this.auth.validateUserRole();
    this.validate();
    this.getHouseListing();
  }
  async getHouseListing() {
    await this.feedsandmedicineConsumption.getHouselist(this.page, this.limit).then(res => {
      console.log('getHouselisting', res);
      this.isLoaded = true;
      if (res['error'] == 0) {
        this.totalItems = res['total_count'];
        this.totalPages = res['total_page'];
        this.prev_page = res['previous_page'];
        this.next_page = res['next_page'];
        if (this.totalItems < 10) {
          this.limit_disabled = this.totalItems;
        }
        res['datas'].forEach(data => {
          this.houses.push(data);
        });
      } else {
        this.houses = [];
        this.totalItems = 0;
        this.totalPages = 0;
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
    });
  }
  async validate() {
    this.feedsandmedicineConsumption.validate().then(res => {
      if (res['error'] == 0) {
        this.canAdd = true;
      } else {
        this.canAdd = false;
      }
    }).catch(e => {
      console.log("e", e);
    });
  }
}
