export interface DialogData {
  page: number,
  limit: number,
  from: any,
  to: any,
  visible_columns: any,
  ths: any
};

@Component({
  selector: 'app-report-overall-sales',
  templateUrl: './report-overall-sales.component.html',
  styleUrls: ['./report-overall-sales.component.scss']
})
export class ReportOverallSalesComponent implements OnInit {
  
  public lineChartOptions: ChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    aspectRatio: 0.8,
    scales: {
      yAxes: [{
        scaleLabel: {
          display: true,
          labelString: 'Total amount of sales',
          fontSize: 16,
          lineHeight: 4,
          fontColor: '#05172e'
        },
        ticks: {
          min: 0,
          beginAtZero: true,
          callback: function(label, index, labels) {
            let SI_POSTFIXES = ["", "K", "M", "G", "T", "P", "E"];
            let tier = Math.log10(Math.abs(label)) / 3 | 0;
            if(tier == 0) return label;
            let postfix = SI_POSTFIXES[tier];
            let scale = Math.pow(10, tier * 3);
            let scaled = label / scale;
            let formatted = scaled.toFixed(1) + '';
            if (/\.0$/.test(formatted))
              formatted = formatted.substr(0, formatted.length - 2);
            return formatted + postfix;
            /* let parts = label.toString().split(".");
            parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            return parts.join(".") + ' g'; */
          }
           /* callback: function(value, index, values) {
            if(parseInt(value) >= 1000){
              return ' ' + value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            } else {
              return ' ' + value;
            }
          } */
           /* stepSize: 1000 */
        }
      }],
      xAxes: [{
        ticks: {
          min: 0,
          beginAtZero: true
        }
      }]
    },
    tooltips: {
      callbacks: {
        title: (title, data) => {
          return null;
        },
        label: function (tooltipItem, data) {
          return tooltipItem.label/* this.datePipe.transform(new Date(tooltipItem.label), 'MMM dd') */;
        },
        footer: function(item, data) {
          console.log("item", item);
          console.log("data", data);
          if (parseInt(item[0].value) >= 1000){
            return 'Sales:  ' + item[0].value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
          } else {
            return 'Sales:  ' + item[0].value;
          }
        }
      }
    },
    plugins: {
      datalabels: {
        color: '#05172e',
        formatter: (value, ctx) => {
          if (value == 0) {
            return '';
          }
          let SI_POSTFIXES = ["", "K", "M", "G", "T", "P", "E"];
          let tier = Math.log10(Math.abs(value)) / 3 | 0;
          if(tier == 0) return value;
          let postfix = SI_POSTFIXES[tier];
          let scale = Math.pow(10, tier * 3);
          let scaled = value / scale;
          let formatted = scaled.toFixed(1) + '';
          if (/\.0$/.test(formatted))
            formatted = formatted.substr(0, formatted.length - 2);
          return formatted + postfix;
        },
      },
    }
    /* animation: {
      onComplete: function () {
        var chartInstance = this.chart,
        ctx = chartInstance.ctx;
        ctx.textAlign = 'center';
        ctx.textBaseline = 'bottom';
        this.data.datasets.forEach(function (dataset, i) {
            var meta = chartInstance.controller.getDatasetMeta(i);
            meta.data.forEach(function (bar, index) {
                var data = dataset.data[index];
                ctx.fillText(data, bar._model.x, bar._model.y - 5);
            });
        });
      }
    } */
  };
  public lineChartLabels: Label[] = [];
  public lineChartType: ChartType = 'line';
  public lineChartLegend = false;
  public lineChartPlugins = [pluginDataLabels];
  public lineChartData: ChartDataSets[] = [];
  chartvalues: any = [];
  chartlabels: any = [];
  overall_value: number = 0;

  isLoaded: boolean = false;

  ths: any = datatable;

  visible_columns: any = [];

  items: any = [];
  item: any = [];
  totalItems: number = 0;
  
  prev_page: number = 0;
  next_page: number = 0;
  totalPages: number = 0;

  page: number = 1;
  limit: number = 10;
  limit_disabled: number = 0;
  order: any = {
    order_by_column: 'created_at',
    order_by: 'asc'
  };
  from: any = null;
  to: any = null;
  created_at: any = [];
  show_filter: boolean = false;
  activity: any = [];
  showtimeago: boolean = true;

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementId: 'printable-section',
    options: {
      jsPDF: {
        orientation: 'landscape',
        format: 'legal',
      },
      /* html2canvas:  {
        scale: 2
      }, */
      margin: 10,
      compress: true,
      pagebreak: {
        /* mode: 'avoid-all', */
        after: '.break-now'
      },
      /* image: {
        type: 'jpeg',
        quality: 0.95
      }, */
      /* compress: true,
      
      html2canvas:  {
        scale: 2
      },
      pagebreak: {
        before: '.break-now'
      },
      fileName: 'asd.pdf', */
      /* pdfCallbackFn: this.pdfCallbackFn */ // to add header and footer
    }
  };
  user_profile: any = [];
  date_today: any = new Date();

  constructor(
    public dialogRef: MatDialogRef<ReportOverallSalesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private formBuilder: FormBuilder,
    private _snackBar: MatSnackBar,
    private dialog: MatDialog,
    private datePipe: DatePipe,
    private eggTypeService: EggTypeService,
    private performanceReportService: PerformanceReportService,
    private router: Router,
    private exportAsService: ExportAsService,
  ) {
    this.ths.forEach((data, index) => {
      this.visible_columns.push(index);
    });
  }

  ngOnInit() {
    let user = JSON.parse(localStorage.getItem("user"));
    this.user_profile = user;
    this.page = this.data.page;
    this.limit = this.data.limit;
    this.from = this.data.from;
    this.to = this.data.to;
    this.visible_columns = this.data.visible_columns;
    this.ths = this.data.ths;
    this.loopDates();
    /* this.getList(); */
  }
  loopDates() {
    let loop = new Date(this.from);
    while (loop <= new Date(this.to)) {
      this.lineChartLabels.push(this.datePipe.transform(new Date(loop), 'MM/dd'));
      this.chartvalues.push(null);
      let newDate = loop.setDate(loop.getDate() + 1);
      loop = new Date(newDate);
    }
    this.getList();
  }
  async getList() {
    await this.performanceReportService.getOverallSales(this.page, this.limit, this.from, this.to, this.order).then(res => {
      if (res['error'] == 0) {
        this.totalItems = res['datas'].length;
        this.totalPages = res['total_page'];
        this.prev_page = res['previous_page'];
        this.next_page = res['next_page'];
        if (this.totalItems < 10) {
          this.limit_disabled = this.totalItems;
        }
        this.item = res['data'];
        res['datas'].forEach(data => {
          this.items.push(data);
        });
      } else {
        this.items = [];
        this.totalItems = 0;
        this.totalPages = 0;
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
    });
    this.getGraph();
  }
  async getGraph() {
    /* this.chartvalues = [];
    this.chartlabels = [];
    this.lineChartLabels = []; */
    this.overall_value = 0;
    this.lineChartData = [];
    this.totalItems = this.totalItems == 1 ? 2 : this.totalItems;
    await this.performanceReportService.getOverallSales(1, this.totalItems, this.from, this.to, this.order).then(res => {
      this.isLoaded = true;
      if (res['error'] == 0) {
        res['datas'].forEach(data => {
          let getIndex = this.lineChartLabels.indexOf(this.datePipe.transform(new Date(data.date), 'MM/dd'));
          this.chartvalues[getIndex] = Number(data.total);
          this.overall_value += Number(data.total);
          /* this.chartlabels.push(this.datePipe.transform(new Date(data.date), 'MM/dd/yy'));
          this.lineChartLabels.push(this.datePipe.transform(new Date(data.date), 'MM/dd/yy'));
          this.chartvalues.push(Number(data.total));
          this.overall_value += Number(data.total); */
        });
        let getMax = Math.max.apply(Math, this.chartvalues);
        let max = getMax / Number(this.visible_columns.length);
        /* this.lineChartOptions.scales.yAxes[0].ticks.max = getMax + max; */
        this.lineChartData = [
          {
            data: this.chartvalues,
            label: 'Sales',
            borderColor: '#FF9F1A',
            backgroundColor: 'transparent',
            hoverBackgroundColor: 'transparent',
          }
        ];
        console.log(this.lineChartData);
      } else {
        this.lineChartData = [
          {
            data: this.chartvalues,
            label: 'Sales',
            borderColor: '#FF9F1A',
            backgroundColor: 'transparent',
            hoverBackgroundColor: 'transparent',
          }
        ];
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
      this.lineChartData = [
        {
          data: this.chartvalues,
          label: 'Sales',
          borderColor: '#FF9F1A',
          backgroundColor: 'transparent',
          hoverBackgroundColor: 'transparent',
        }
      ];
    });
  }
  async exportPdf(type: SupportedExtensions, opt?: string) {
    let that = this;
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    let fileName = 'Report-Sales-Overall-' + this.datePipe.transform(new Date(), 'yyyy-MM-dd');
    this.exportAsService.save(this.exportAsConfig, fileName).subscribe(() => {
      this.dialogRef.close();
    });
  }
}
