import { Component, OnInit } from '@angular/core';
import { PerformanceReportService } from './../../../../../services/performance-report/performance-report.service';
import { AuthService } from './../../../../../services/auth/auth.service';
import { DatePipe } from '@angular/common';
import { ChartOptions, ChartType, ChartDataSets } from 'chart.js';
import * as pluginDataLabels from 'chartjs-plugin-datalabels';
import { Label } from 'ng2-charts';
import { MatDialog } from '@angular/material/dialog';
import { ReportMedicineComponent } from './../../../../../components/modals/reports/report-medicine/report-medicine.component';

@Component({
  selector: 'app-medicine',
  templateUrl: './medicine.component.html',
  styleUrls: ['./medicine.component.scss']
})
export class MedicineComponent implements OnInit {

  public barChartOptions: ChartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    aspectRatio: 0.8,
    scales: {
      yAxes: [{
        stacked: true,
        scaleLabel: {
          display: true,
          labelString: 'Total Consumed',
          fontSize: 16,
          lineHeight: 4,
          fontColor: '#05172e'
        },
        ticks: {
           min: 0,
           callback: function(label, index, labels) {
            let SI_POSTFIXES = ["", "K", "M", "G", "T", "P", "E"];
            let tier = Math.log10(Math.abs(label)) / 3 | 0;
            if(tier == 0) return label;
            let postfix = SI_POSTFIXES[tier];
            let scale = Math.pow(10, tier * 3);
            let scaled = label / scale;
            let formatted = scaled.toFixed(1) + '';
            if (/\.0$/.test(formatted))
              formatted = formatted.substr(0, formatted.length - 2);
            return formatted + postfix;
            /* let parts = label.toString().split(".");
            parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            return parts.join(".") + ' g'; */
          }
        }
      }],
      xAxes: [{
        stacked: true
      }]
    },
    legend: {
      position: 'top',
      /* align: 'start',
      labels: {
        padding: 5
      } */
    },
    tooltips: {
      callbacks: {
        /* title: (title, data) => {
          return null;
        }, */
        label: function(item, data) {
          let index = item.index;
          let datasetIndex = item.datasetIndex;
          let total = 0;
          data.datasets.forEach(dset => {
            total += Number(dset.data[index]);
          });
          let label = data.datasets[datasetIndex].label;
          /* return "a"; */
          if (Number(item.value) == 0) {
            return null;
          } else {
            return label;
          }
        },
        footer: function(item, data) {
          let index = item[0].index;
          let overall: any = 0;
          data.datasets.forEach(dset => {
            if (!dset.hidden) {
              overall += Number(dset.data[index]);
            }
          });

          let piece: any = 0;
          if (Number(item[0].value) != 0) {
            let parts = item[0].value.toString().split(".");
            parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            piece = parts.join(".");
          }
          let parts = overall.toString().split(".");
          parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
          return "Total: " + piece + " ml\nOverall: " + parts.join(".") + " ml";
        }
      }
    },
    plugins: {
      datalabels: {
        formatter: (value, ctx) => {
          return null;
        },
      },
    },
  };
  public barChartLabels: Label[] = [];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [pluginDataLabels];
  public barChartData: ChartDataSets[] = [];

  chartvalues: any = [];
  chartlabels: any = [];

  isLoaded: boolean = false;

  ths: any = [];
  visible_columns: any = [];

  totalItems: number = 0;
  prev_page: number = 0;
  next_page: number = 0;
  totalPages: number = 0;

  page: number = 1;
  limit: number = 10;
  limit_disabled: number = 0;
  order: any = {
    order_by_column: 'id',
    order_by: 'asc'
  };
  from: any = null;
  to: any = null;
  items: any = [];
  max_date: any = new Date();
  medicines: any = [];
  houses: any = [];
  med_id: any;
  house_id: any;
  month: any;
  
  created_at: any = [];

  activity: any = [];

  charthousevalues: any = [];

  constructor(
    private auth: AuthService,
    private performanceReportService: PerformanceReportService,
    private datePipe: DatePipe,
    private dialog: MatDialog
  ) { 
    this.ths.forEach((data, index) => {
      this.visible_columns.push(index);
    });
    let dateToday = new Date();
    let year = dateToday.getFullYear();
    let month = dateToday.getMonth();
    this.from = this.datePipe.transform(new Date(year, month, 1), 'yyyy-MM-dd');
    this.to = this.datePipe.transform(new Date(year, month + 1, 0), 'yyyy-MM-dd');
    this.created_at = {
      begin: this.from,
      end: this.to
    };
    this.loopDates();
  }
  loopDates() {
    let loop = new Date(this.from);
    while (loop <= new Date(this.to)) {
      this.barChartLabels.push(this.datePipe.transform(new Date(loop), 'MM/dd'));
      /* this.chartvalues.push(0); */
      let newDate = loop.setDate(loop.getDate() + 1);
      loop = new Date(newDate);
    }
  }
  ngOnInit() {
    this.auth.validateUserRole();
    /* this.getList(); */
    this.getAllmedicines();
    this.getAllhouse();
  }

  async getList() {
    await this.performanceReportService.getMedicineconsumption(this.page, this.limit, this.from, this.to, this.order, this.month ,this.house_id, this.med_id).then(res => {
      console.log("medicineConsumption", res);
      if (res['error'] == 0) {
        this.totalItems = res['total_count'];
        this.totalPages = res['total_page'];
        this.prev_page = res['previous_page'];
        this.next_page = res['next_page'];
        if (this.totalItems < 10) {
          this.limit_disabled = this.totalItems;
        }
        res['datas'].forEach(data => {
          this.items.push(data);
        });
      } else {
        this.items = [];
        this.totalItems = 0;
        this.totalPages = 0;
      }
    }).catch(e => {
      this.isLoaded = true;
      console.log("e", e);
    });
    this.getGraph();
  }
  async getGraph() {
    this.barChartData = [];
    this.chartvalues = [];
    this.charthousevalues.forEach(chv => {
      chv.data.forEach((e, index) => {
        chv.data[index] = 0;
      });
    });
    this.totalItems = this.totalItems == 1 ? 2 : this.totalItems;
    await this.performanceReportService.getMedicineconsumption(1, this.totalItems, this.from, this.to, this.order, this.month ,this.house_id, this.med_id).then(res => {
      this.isLoaded = true;
      if (res['error'] == 0) {
        res['datas'].forEach(data => {
          console.log("datad", data);
          let index_of_date = this.barChartLabels.indexOf(this.datePipe.transform(new Date(data.date), 'MM/dd'));
          console.log("index_of_date", index_of_date);
          this.charthousevalues.forEach(chv => {
            console.log("chv", chv);
            let findhome = data.house.find(x => x.id == chv.house_id);
            if (findhome/*  && findhome.medicine_volume */) {
              chv.data[index_of_date] = findhome.medicine_volume;
              let findIfVisible = this.ths.find(x => x.key == chv.house_id);
              chv.hidden = !findIfVisible.isVisible;
            }
          });
        });
        console.log("this.charthousevalues", this.charthousevalues);
        this.barChartData = this.charthousevalues;
      } else {
        this.barChartData = this.charthousevalues;
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
      this.barChartData = this.charthousevalues;
    });
  }
    counter(i: number) {
    return new Array(i);
  }
  async getAllmedicines() {
    await this.performanceReportService.getAllmedicine().then(res => {
      console.log('medicines', res);
      if (res['error'] == 0) {
        res['data'].forEach(data => {
          this.medicines.push(data);
        });
      } else {
        this.medicines = [];
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
    });
  }
  async getAllhouse() {
    await this.performanceReportService.getAllList().then(res => {
      console.log('houses', res);
      if (res['error'] == 0) {
        res['datas'].forEach(data => {
          this.houses.push(data);
          this.ths.push({
            label: 'House/Bldg no. ' + data.house_name,
            key: data.id,
            canSort: false,
            isVisible: true,
            canToggle: true
          }); 
        });
        this.ths.forEach((data, index) => {
          this.visible_columns.push(index);
          this.charthousevalues.push({
            data: [],
            house_id: data.key,
            label: data.label,
            /* backgroundColor: '#FFC575',
            hoverBackgroundColor: '#FFB247', */
          });
          this.barChartLabels.forEach(() => {
            this.charthousevalues[index].data.push(0);
          });
        });
      } else {
        this.houses = [];
      }
    }).catch(e => {
      console.log("e", e);
      this.isLoaded = true;
    });
    this.getList();
  }
  async changeHouse(e){
    this.isLoaded = false;
    this.houses = [];
    await this.getAllhouse();
  }
  selectMed(id){
    this.med_id = id;
    this.items = [];
    this.getList();
  }
  selectHouse(e){
    this.items = [];
    this.getList();
  }
  async changeColumnVisibility(e) {
    console.log("e", e);
    this.ths.forEach((data, index) => {
      data.isVisible = this.visible_columns.some(e => e == index);
    });
    this.getGraph();
  }
  async chooseCreatedAt(e) {
    this.from = this.datePipe.transform(new Date(this.created_at.begin), 'yyyy-MM-dd');
    this.to = this.datePipe.transform(new Date(this.created_at.end), 'yyyy-MM-dd');
    this.isLoaded = false;
    this.items = [];
    this.totalItems = 0;
    this.totalPages = 0;
    this.chartvalues = [];
    this.barChartLabels = [];
    this.loopDates();
    await this.getList();
  }
  async clearFilters() {
    let dateToday = new Date();
    let year = dateToday.getFullYear();
    let month = dateToday.getMonth();
    this.from = this.datePipe.transform(new Date(year, month, 1), 'yyyy-MM-dd');
    this.to = this.datePipe.transform(new Date(/* dateToday */year, month + 1, 0), 'yyyy-MM-dd');
    this.created_at = {
      begin: this.from,
      end: this.to
    };
    this.isLoaded = false;
    this.page = 1;
    this.items = [];
    this.totalItems = 0;
    this.totalPages = 0;
    this.chartvalues = [];
    this.barChartLabels = [];
    this.loopDates();
    await this.getList();
  }
  async gotoPage(page) {
    if (this.page != page) {
      this.isLoaded = false;
      this.page = page;
      this.items = [];
      this.totalItems = 0;
      this.totalPages = 0;
      await this.getList();
    }
  }
  downloadModal() {
    let dialog = this.dialog.open(ReportMedicineComponent, {
      panelClass: "scroll",
      data: {
        page: 1,
        limit: this.totalItems,
        from: this.from,
        to: this.to,
        order: this.order,
        ths: this.ths,
        visible_columns: this.visible_columns,
        med_id: this.med_id,
        barChartLabels: this.barChartLabels,
        charthousevalues: this.charthousevalues
      }
    });
    dialog.afterClosed().subscribe(result => {

    });
  }
}
